import axios from "axios";
// import qs from 'qs'
import { Message, Loading } from "element-ui";

/**
 * config
 * @param isLoading loading
 */
let customOptions = {
  isLoading: true,
};
// 
let requestCount = 0;
// loading
let loading;

const instance = axios.create({
  baseURL: process.env.VUE_APP_BASE_URL,
  // baseURL: 'http://192.168.57.198:9000/',
  timeout: 10000 * 5,
  withCredentials: true, //cookie
  // headers: { 'X-Custom-Header': 'foobar' },
});
// 
instance.interceptors.request.use(
  (config) => {
    // 
    config = { ...customOptions, ...config };
    config.headers["Content-Type"] = "application/json";
    requestCount++;
    if (requestCount === 1 && config.isLoading) {
      // loading = Loading.service()
      loading = Loading.service({
        lock: true,
        text: "Loading",
        target: document.querySelector(".el-main"),
        spinner: "el-icon-loading",
        background: "rgba(255, 255, 255, 0.7)",
      });
    }
    return config;
  },
  (error) => {
    // 
    return Promise.reject(error);
  }
);

// 
instance.interceptors.response.use(
  (response) => {
    // console.log(response, 'response')
    // 
    requestCount--;
    if (requestCount === 0 && loading) {
      loading.close();
    }
    // responseType
    if (!response.config.responseType) {
      const { result, msg } = response.data;
      if (result) return response.data;
      else {
        Message.error(msg);
        return Promise.reject(response.data);
      }
    }
    return response.data;
  },
  (error) => {
    let message = "";
    if (error && error.response) {
      switch (error.response.status) {
        case 302:
          message = "";
          break;
        case 401:
          message = "";
          break;
        case 403:
          message = "";
          break;
        case 404:
          message = `: ${error.response.config.url}`;
          break; // 
        case 500:
          message = "";
          break;
        case 501:
          message = "";
          break;
        case 502:
          message = "";
          break;
        case 503:
          message = "";
          break;
        case 504:
          message = "";
          break;
        case 505:
          message = "HTTP";
          break;
        default:
          message = "";
          break;
      }
    } else if (error.message.includes("timeout")) {
      message = "";
    } else {
      message = "";
    }
    requestCount--;
    loading.close();
    Message.error(message);
    return Promise.reject(error);
  }
);

export default instance;
